.App {
  background-color: #87b5b5;
  width: 100%;
  height: auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.maximised {
  height: 97vh;
}
.maximised .editor-textarea {
  resize: none !important;
}

.hidden {
  display: none;
}

.previewer-container {
  margin-top: 20px;
  width: 750px;
  height: auto;
  min-height: 250px;
  border-radius: 5px;
  background-color: #c0d8d8;
  border: 2px solid #000000;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
  display: flex;
  flex-direction: column;
}

